  width: 100%;
  border: none;
}

table td {
  padding: 0 10px 0 0;
  border: none;
}

.hidden,
.screen_reader,
hr {
  display: none;
}

p {
  margin: 0 0 1em;
  padding: 0;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

a {
  text-decoration: underline;
  color: #000;
}

a:hover {
  text-decoration: none;
  color: #bb4d4d;
}

.floatL {
  float: left;
}

.floatR {
  float: right;
}

.alignleft {
  float: left;
  margin: 0 25px 25px 0;
}

.alignright {
  float: right;
  margin: 0 0 14px 25px;
}

.alignnone,
.aligncenter {
  padding: 20px 0;
}

.aligncenter {
  text-align: center;
  margin: 0 auto;
  display: block;
}

.italic {
  font-style: italic;
}

.upper {
  text-transform: uppercase;
}

.clear {
  clear: both;
}

.hideText {
  text-indent: -3000em;
  overflow: hidden;
}

.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

blockquote {
  padding: 20px;
  margin: 0 40px 20px 20px;
  clear: both;
}

* {
  -webkit-box-sizing: border-box;
